import { createTheme } from "@mui/material";
import { red } from "@mui/material/colors";

export const darkThemeLTR = createTheme({
  direction: "ltr",
  palette: {
    mode: "dark",
    background: {
      default: "#13151f",
      paper: "#13151f"
    },
    primary: {
      main: '#556cd6',
    },
    secondary: {
      main: '#19857b',
    },
    error: {
      main: red.A400,
    }
  }
});

export const darkThemeRTL = createTheme({
  ...darkThemeLTR,
  direction: "rtl"
});