import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import Link from "@mui/material/Link";
import Grid from "@mui/material/Grid";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Container from "@mui/material/Container";
import LanguageSelect from "../components/LanguageSelect";
import { CssBaseline } from "@mui/material";
import { FormattedMessage } from "react-intl";
import { Link as RouterLink } from "react-router-dom";

export default function Login({ ...props }) {
  const handleSubmit = (event) => {
    event.preventDefault();
    const data = new FormData(event.currentTarget);
    console.log({
      email: data.get("email"),
      password: data.get("password"),
    });
  };

  return (
    <>
      <CssBaseline />
      <Container component="main" maxWidth="xs">
        <Box
          sx={{  
            marginTop: 8,
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
          }}
        >
          <Typography component="h1" variant="h5">
            <FormattedMessage id="signin.title" />
          </Typography>
          <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
            <TextField
              margin="normal"
              required
              fullWidth
              id="email"
              label={<FormattedMessage id="signin.emailLabel" />}
              name="email"
              autoComplete="email"
              autoFocus
            />
            <TextField
              margin="normal"
              required
              fullWidth
              name="password"
              label={<FormattedMessage id="signin.passwordLabel" />}
              type="password"
              id="password"
              autoComplete="current-password"
            />
            <FormControlLabel
              control={<Checkbox value="remember" color="primary" />}
              label={<FormattedMessage id="signin.rememberMe" />}
            />
            <Button
              type="submit"
              fullWidth
              variant="contained"
              sx={{ mt: 3, mb: 2 }}
            >
              <FormattedMessage id="signin.button" />
            </Button>
            <Grid container>
              <Grid item xs>
                <Link to="/forgot-password" component={RouterLink}>
                  <FormattedMessage id="signin.forgotPassword" />
                </Link>
              </Grid>
              <Grid item>
                <Link to="/signup" component={RouterLink}>
                  <FormattedMessage id="signin.noAccount" />
                </Link>
              </Grid>
            </Grid>
          </Box>
        </Box>

        <Box component="footer" maxWidth="xs" sx={{ mt: 2 }}>
          <LanguageSelect {...props} />
        </Box>
      </Container>
    </>
  );
}