import englishMessages from "../locales/en.json";
import arabicMessages from "../locales/ar.json";

export const supportedLocales = [
  {
    name: "English (US)",
    code: "en",
    dir: "ltr",
    messages: englishMessages
  },
  {
    name: "العربية",
    code: "ar",
    dir: "rtl",
    messages: arabicMessages
  },
]

export const defaultLocale = supportedLocales[0];

export function getDeviceLocale () {
  const navigatorLanguage = navigator.language || navigator.userLanguage;
  
  if (navigatorLanguage) {
    const languageCode = navigatorLanguage.split("-")[0];
    return supportedLocales.find((locale) => locale.code === languageCode) || defaultLocale;
  }

  return defaultLocale;
}

export function resolveLocale (query) {
  return supportedLocales.find((locale) => locale.code === query);
}