import React from "react";
import { AppBar, Typography, IconButton, Toolbar, Button } from "@mui/material";
import { FormattedMessage } from "react-intl";
import { Menu as MenuIcon } from "@mui/icons-material";
import LanguageSelect from "./LanguageSelect";
import { Link } from "react-router-dom";

export default function DrawerAppBar ({ drawerWidth, handleDrawerToggle, ...props }) {
  return (
    <AppBar color="secondary" elevation={1} position="fixed" sx={{ width: { sm: `calc(100% - ${drawerWidth}px)` }, ml: { sm: `${drawerWidth}px` }}}>
      <Toolbar>
        <IconButton color="inherit" aria-label="open drawer" edge="start" onClick={handleDrawerToggle} sx={{ mr: 2, display: { sm: 'none' } }}>
          <MenuIcon />
        </IconButton>
        <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
          STEEL
        </Typography>
        <LanguageSelect {...props} />
        <Button color="primary" variant="contained" LinkComponent={Link} to="/login">
          <FormattedMessage id="drawerAppBar.login" />
        </Button>
      </Toolbar>
    </AppBar>
  )
}