import { createTheme } from "@mui/material";

export const lightThemeLTR = createTheme({
  direction: "ltr",
  palette: {
    mode: "light",
    background: {
      default: "#f7f7f7"
    },
    text: {
      primary: "#000",
      secondary: "#1a1a1a"
    },
    secondary: {
      main: '#fff',
    }
  },
});

export const lightThemeRTL = createTheme({
  ...lightThemeLTR,
  direction: "rtl"
});