import React, { useEffect, useState } from "react";
import { BrowserRouter, Route, Routes } from "react-router-dom";
import { CacheProvider, ThemeProvider } from "@emotion/react";
import { IntlProvider } from "react-intl"; 
import { prefixer } from "stylis";
import rtlPlugin from "stylis-plugin-rtl";
import createCache from "@emotion/cache";
import { lightThemeLTR, lightThemeRTL } from "./theme/lightTheme";
import { darkThemeLTR, darkThemeRTL } from "./theme/darkTheme";
import { getDeviceLocale, resolveLocale } from "./utils/Locale";
import Home from "./pages/Home";
import SignIn from "./pages/SignIn";
import "./App.css";

const cacheRtl = createCache({
  key: "muirtl",
  stylisPlugins: [prefixer, rtlPlugin],
});

const cacheLtr = createCache({
  key: "muiltr",
  stylisPlugins: [prefixer]
});

export default function App() {
  const [locale, setLocale] = useState((resolveLocale(localStorage.getItem("locale")) || getDeviceLocale()).code);
  const [theme, setTheme] = useState(localStorage.getItem("theme") || "light");
  const [dir, setDir] = useState("ltr");
  const props = {theme, dir, locale, setTheme, setDir, setLocale };

  useEffect(() => {
    if (locale) {
      const resolvedLocale = resolveLocale(locale);
      window.document.dir = resolvedLocale.dir;
      localStorage.setItem("locale", resolvedLocale.code);
      setDir(resolvedLocale.dir);
    }

    localStorage.setItem("theme", theme);
  }, [theme, locale]);

  return (
    <IntlProvider locale={locale} messages={resolveLocale(locale).messages}>
      <CacheProvider value={dir === "ltr" ? cacheLtr : cacheRtl}>
        <ThemeProvider theme={
            theme === "light"
              ? dir === "ltr" ? lightThemeLTR : lightThemeRTL
              : dir === "ltr" ? darkThemeLTR : darkThemeRTL
        }>
          <BrowserRouter>
            <Routes>
              <Route path="/" element={<Home {...props} />} />
              <Route path="/signin" element={<SignIn {...props} />} />
            </Routes>
          </BrowserRouter>
        </ThemeProvider>
      </CacheProvider>
    </IntlProvider>
  );
}