import React from "react";
import { Divider, List, ListItem, ListItemButton, ListItemIcon, ListItemText, Toolbar } from "@mui/material";
import { Search as SearchIcon, Home as HomeIcon, People as PeopleIcon, Wallet as WalletIcon } from "@mui/icons-material";
import { FormattedMessage } from "react-intl";
import ThemeSwitch from "./ThemeSwitch";
import { Link, useLocation } from "react-router-dom";

export default function DrawerMenu ({ theme, setTheme, ...props }) {
  const location = useLocation();

  const ListItemComponent = ({ icon, text, pathname }) => (
    <ListItem disablePadding>
      <ListItemButton selected={pathname === location.pathname} LinkComponent={Link} to={pathname}>
        <ListItemIcon>{icon}</ListItemIcon>
        <ListItemText primary={text} />
      </ListItemButton>
    </ListItem>
  );

  return (
    <div>
      <Toolbar>
        <ThemeSwitch checked={theme !== "light"} onChange={() => setTheme(theme==="light"?"dark":"light")} />
      </Toolbar>
      <Divider />
      <List>
        <ListItemComponent icon={<HomeIcon />} text={<FormattedMessage id="drawerMenu.home" />} pathname="/" />
        <ListItemComponent icon={<SearchIcon />} text={<FormattedMessage id="drawerMenu.browse" />} pathname="/browse" />
      </List>
      <Divider />
      <List>
        <ListItemComponent icon={<PeopleIcon />} text={<FormattedMessage id="drawerMenu.profile" />} pathname="/profile" />
        <ListItemComponent icon={<WalletIcon />} text={<FormattedMessage id="drawerMenu.wallet" />} pathname="/wallet" />
      </List>
    </div>
  )
}