import React, { useState } from "react";
import { BottomNavigation, BottomNavigationAction, Paper } from "@mui/material";
import { Home as HomeIcon, People as PeopleIcon, Wallet as WalletIcon } from "@mui/icons-material";
import { FormattedMessage } from "react-intl";

export default function LabelBottomNavigation () {
  const [value, setValue] = useState(0);

  return (
    <Paper sx={{ display: { sm: "none" }, position: "fixed", bottom: 0, left: 0, right: 0 }}>
      <BottomNavigation
        showLabels
        value={value}
        onChange={(event, newValue) => {
          setValue(newValue);
        }}
      >
        <BottomNavigationAction label={<FormattedMessage id="bottomNavigation.home" />} icon={<HomeIcon />} />
        <BottomNavigationAction label={<FormattedMessage id="bottomNavigation.profile" />} icon={<PeopleIcon />} />
        <BottomNavigationAction label={<FormattedMessage id="bottomNavigation.wallet" />} icon={<WalletIcon />} />
      </BottomNavigation>
    </Paper>
  )
}